import { experiences } from "../../env/variables";

export default function Experience(props) {
  const { t = {} } = props;
  return (
    <div className="experiences" id="experience">
      <h2 className="title bigTilte">{t.workExperience}</h2>

      <div className="experiences-container">
        {experiences.map((exp, index) => (
          <div className="section" key={index}>
            <h3 className="section-title">{exp.title}</h3>
            <p className="section-company">{exp.company}</p>
            <p className="section-date">{exp.date}</p>
            <p>{exp.type}</p>
          </div>
        ))}
      </div>
    </div>
  );
}
