import { education } from "../../env/variables";

export default function Education(props) {
  const { t = {} } = props;
  return (
    <div className="educations" id="education">
      <h2 className="title bigTilte">{t.education}</h2>

      <div className="educations-container">
        {education.map((edu, index) => (
          <div key={index} className="section">
            <h3 className="section-degree">{edu.degree}</h3>
            <p className="section-institution">{edu.institution}</p>
            <p className="section-date">{edu.date}</p>
          </div>
        ))}
      </div>
    </div>
  );
}
