import React, { useState, useEffect, useRef } from "react";
import {
  motion,
  useScroll,
  useInView,
  useMotionValueEvent,
  useSpring,
  MotionConfig,
} from "framer-motion";
import { ThemeContext } from "./env/theme";
import { LanguageContext, translations } from "./env/language";
import Navbar from "./component/navbar";
import ChatComponent from "./component/chatbot/chatbot";
import Header from "./component/header";
import SkillsComponent from "./sections/skills/skills";
import Experience from "./sections/experience/experience";
import Education from "./sections/edu/education";
import Language from "./sections/lang/language";
import Contact from "./sections/contact/contact";
import Footer from "./component/footer";

function Section({ component }) {
  const ref = useRef(null);
  const isInView = useInView(ref, { amount: 0.5, once: false });

  return (
    <section ref={ref}>
      <span
        style={{
          opacity: isInView ? 1 : 0.3,
          filter: isInView ? "none" : "blur(4px)",
          transition: "all 0.9s ease-in-out",
        }}
      >
        {component}
      </span>
    </section>
  );
}

function App() {
  const [theme, setTheme] = useState("light");
  const [language, setLanguage] = useState("en");
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [isChatBotOpen, setIsChatBotOpen] = useState(false);
  const step = [
    "about",
    "skills",
    "experience",
    "education",
    "languages",
    "contact",
  ];
  const t = translations;

  const getSection = (step) => {
    switch (step) {
      case "about":
        return <Header t={t[language]} />;
      case "skills":
        return <SkillsComponent t={t[language]} />;
      case "experience":
        return <Experience t={t[language]} />;
      case "education":
        return <Education t={t[language]} />;
      case "languages":
        return <Language t={t[language]} />;
      case "contact":
        return <Contact t={t[language]} />;
      default:
        break;
    }
  };

  const toggleTheme = () => {
    setTheme((prevTheme) => (prevTheme === "dark" ? "light" : "dark"));
  };
  useEffect(() => {
    document.documentElement.setAttribute("data-theme", theme);
  }, [theme]);

  const { scrollYProgress } = useScroll();
  const scaleX = useSpring(scrollYProgress, {
    stiffness: 100,
    damping: 30,
    restDelta: 0.001,
  });
  const [state, setState] = useState(0);
  useMotionValueEvent(scrollYProgress, "change", (latest) => {
    setState(latest.toFixed(2) * 100);
  });

  return (
    <ThemeContext.Provider value={{ theme, toggleTheme }}>
      <LanguageContext.Provider value={{ language, setLanguage }}>
        <MotionConfig transition={2}>
          <div className={`container`}>
            <Navbar
              t={t[language]}
              toggleTheme={toggleTheme}
              theme={theme}
              language={language}
              setLanguage={setLanguage}
              isMenuOpen={isMenuOpen}
              setIsMenuOpen={setIsMenuOpen}
            />
            <main>
              {step.map((step) => (
                <Section key={step} id={step} component={getSection(step)} />
              ))}
            </main>
            <motion.div className="progress" style={{ scaleX }} />
            <motion.div className="progress-percentage">{state} %</motion.div>

            <Footer t={t[language]} />
            <ChatComponent
              lang={language}
              chatOpen={isChatBotOpen}
              setChatOpen={setIsChatBotOpen}
            />
          </div>
        </MotionConfig>
      </LanguageContext.Provider>
    </ThemeContext.Provider>
  );
}

export default App;
