import { languages } from "../../env/variables";
import { CircularProgress } from "@mui/joy";

export default function Language(props) {
  const { t = {} } = props;
  return (
    <div className="languages" id="languages">
      <h2 className="title bigTilte">{t.languages}</h2>
      <div className="languages-container">
        {languages.map((lang) => (
          <div key={lang.language} className="section">
            <div className="section-progress">
              <CircularProgress
                size="lg"
                determinate
                value={lang.level}
                variant="soft"
                sx={{
                  color: "var(--text-200)",
                  "--CircularProgress-trackThickness": "var(--font-size-p)",
                  "--CircularProgress-size": "calc(var(--font-size-h1)*5)",
                  "--CircularProgress-progressThickness": "var(--font-size-p)",
                  "--CircularProgress-progressColor": "var(--accent-200)",
                  "--CircularProgress-linecap": "none",
                  "--CircularProgress-trackColor": "var(--accent-100)",
                }}
              >
                <p
                  style={{
                    fontSize: "var(--font-size-h3)",
                    textAlign: "center",
                  }}
                >
                  {lang.language}
                  <br />
                  <span
                    style={{
                      fontSize: "var(--font-size-p)",
                      color: "var(--accent-200)",
                    }}
                  >
                    {lang.level}%
                  </span>
                </p>
              </CircularProgress>
            </div>
            <p className="section-langue">{lang.languageName}</p>
          </div>
        ))}
      </div>
    </div>
  );
}
