import React, { useEffect, useRef, useState } from "react";
import { motion, AnimatePresence } from "framer-motion";
import { CircularProgress, Box } from "@mui/material";
import { generateRes } from "./response";
import { translate } from "./language";
import { styled } from "@mui/material/styles";

const ScrollArea = styled(Box)(() => ({
  overflowY: "auto",
  flexGrow: 1,
}));

const Message = ({ content, isUser }) => (
  <motion.div
    initial={{ opacity: 0, y: 50, scale: 0.3 }}
    animate={{ opacity: 1, y: 0, scale: 1 }}
    transition={{ duration: 0.3 }}
    className={`message ${isUser ? "user" : "bot"}`}
  >
    <p>{content}</p>
  </motion.div>
);

const ChatbotComponent = (props) => {
  const { lang = "en", chatOpen = false, setChatOpen = () => {} } = props;
  const t = translate;
  const [messages, setMessages] = useState([
    {
      role: "bot",
      content: t[lang].bot_open_msg,
    },
  ]);
  const [isTyping, setIsTyping] = useState(false);
  const [currentOptions, setCurrentOptions] = useState([
    {
      text: t[lang].education,
      value: "education",
      subOptions: [
        { text: "2021-2023", value: "years_2021_2023" },
        { text: "2020-2021", value: "years_2020_2021" },
        { text: "2019-2020", value: "years_2019_2020" },
      ],
    },
    {
      text: t[lang].experience,
      value: "experience",
      subOptions: [
        { text: t[lang].experiences.fullstack, value: "full_stack_developer" },
        {
          text: `${t[lang].experiences.internship} 2023-2024`,
          value: "internship_2023_2024",
        },
        {
          text: `${t[lang].experiences.internship} 2023`,
          value: "internship_2023",
        },
      ],
    },
    {
      text: t[lang].skill,
      value: "skills",
      subOptions: [
        {
          text: t[lang].skills.programming_language,
          value: "programming_languages",
          subOptions: [
            { text: "HTML", value: "html" },
            { text: "CSS", value: "css" },
            { text: "JavaScript", value: "javascript" },
            { text: "PHP", value: "php" },
            { text: "SQL", value: "sql" },
            { text: "Python", value: "python" },
            { text: "C# and C++", value: "csharp_cpp" },
          ],
        },
        {
          text: t[lang].skills.frameworks,
          value: "frameworks",
          subOptions: [
            { text: "React", value: "react" },
            { text: "Laravel", value: "laravel" },
            { text: "Bootstrap", value: "bootstrap" },
            { text: "SCSS", value: "scss" },
            { text: "jQuery", value: "jquery" },
          ],
        },
        {
          text: t[lang].skills.tools,
          value: "tools",
          subOptions: [
            {
              text: t[lang].skills.toolslist.version_control,
              value: "version_control",
            },
            {
              text: t[lang].skills.toolslist.ides,
              value: "ides",
            },
            {
              text: t[lang].skills.toolslist.api_testing,
              value: "api_testing",
            },
            {
              text: t[lang].skills.toolslist.package_managers,
              value: "package_managers",
            },
            {
              text: t[lang].skills.toolslist.build_tools,
              value: "build_tools",
            },
            {
              text: t[lang].skills.toolslist.design_tools,
              value: "design_tools",
            },
            {
              text: t[lang].skills.toolslist.threeD_modeling,
              value: "3d_modeling",
            },
            {
              text: t[lang].skills.toolslist.database_management,
              value: "database_management",
            },
          ],
        },
        { text: t[lang].skills.soft_skills, value: "soft_skills" },
      ],
    },
    {
      text: t[lang].language,
      value: "languages",
      subOptions: [
        { text: t[lang].languages.arabic, value: "arabic" },
        { text: t[lang].languages.tamazight, value: "tamazight" },
        { text: t[lang].languages.french, value: "french" },
        { text: t[lang].languages.english, value: "english" },
      ],
    },
    {
      text: t[lang].contact,
      value: "contact",
      subOptions: [
        { text: t[lang].contactList.email, value: "email" },
        { text: t[lang].contactList.phone, value: "phone" },
        { text: t[lang].contactList.address, value: "address" },
      ],
    },
    {
      text: t[lang].hobbies,
      value: "hobbies",
      subOptions: [
        {
          text: t[lang].hobbiesList.personal_development,
          value: "personal_development",
        },
        {
          text: t[lang].hobbiesList.game_development,
          value: "game_development",
        },
        {
          text: t[lang].hobbiesList.video_gaming,
          value: "video_gaming",
        },
        {
          text: t[lang].hobbiesList.threeD_modeling_hobby,
          value: "3d_modeling_hobby",
        },
        {
          text: t[lang].hobbiesList.tech_blogging,
          value: "tech_blogging",
        },
      ],
    },
  ]);
  const [optionsStack, setOptionsStack] = useState([]);

  const scrollAreaRef = useRef(null);

  useEffect(() => {
    if (scrollAreaRef.current) {
      scrollAreaRef.current.scrollTop = scrollAreaRef.current.scrollHeight;
    }
  }, [messages]);

  const handleOptionClick = (option) => {
    setMessages((prev) => [...prev, { role: "user", content: option.text }]);
    setIsTyping(true);

    if (option.subOptions) {
      setOptionsStack((prev) => [...prev, currentOptions]);
      setCurrentOptions(option.subOptions);
      setIsTyping(false);
    } else {
      setTimeout(() => {
        const botResponse = generateResponse(option.value);
        setMessages((prev) => [...prev, { role: "bot", content: botResponse }]);
        setIsTyping(false);
      }, 1000 + Math.random() * 1000); // Random delay between 1-2 seconds
    }
  };

  const handleBackClick = () => {
    if (optionsStack.length > 0) {
      const previousOptions = optionsStack[optionsStack.length - 1];
      setCurrentOptions(previousOptions);
      setOptionsStack((prev) => prev.slice(0, -1));
    }
  };

  const generateResponse = (query) => generateRes(query, t[lang].responses);

  return (
    <>
      {!chatOpen && (
        <motion.button
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          exit={{ opacity: 0, y: 20 }}
          className="chatbot-button"
          onClick={() => setChatOpen(!chatOpen)}
        >
          <i className="fas fa-robot"></i>
        </motion.button>
      )}
      {chatOpen && (
        <motion.div
          initial={{ opacity: 0, y: 60, x: 10 }}
          animate={{ opacity: 1, y: 0, x: 0 }}
          exit={{ opacity: 0, y: 60, x: 10 }}
          className="chatbot"
        >
          <div className="chatbot-container">
            <div className="chatbot-header">
              <h5>{t[lang].chat_header}</h5>
              <button
                onClick={() => setChatOpen(!chatOpen)}
                className="chatbot-close-btn"
              >
                <i className="fas fa-window-close"></i>
              </button>
            </div>
            <ScrollArea ref={scrollAreaRef}>
              <div className="chatbot-content">
                <AnimatePresence>
                  {messages.map((message, index) => (
                    <Message
                      key={index}
                      content={message.content}
                      isUser={message.role === "user"}
                    />
                  ))}
                </AnimatePresence>
                {isTyping && (
                  <div>
                    <CircularProgress size={20} />
                    <h4>Typing...</h4>
                  </div>
                )}
              </div>
            </ScrollArea>
            <motion.div
              className="chatbot-option"
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              exit={{ opacity: 0, y: 20 }}
            >
              {optionsStack.length > 0 && (
                <button
                  onClick={handleBackClick}
                  className="chatbot-option-back-arrow"
                >
                  <i className="fas fa-reply"></i>
                </button>
              )}
              <div className="chatbot-options">
                {currentOptions.map((option, index) => (
                  <button
                    key={index}
                    onClick={() => handleOptionClick(option)}
                    disabled={isTyping}
                  >
                    {option.text}
                  </button>
                ))}
              </div>
            </motion.div>
          </div>
        </motion.div>
      )}
    </>
  );
};

export default ChatbotComponent;
