import { motion } from "framer-motion";
import { skills } from "../../env/variables";

export default function SkillsComponent(props) {
  const { t = {} } = props;
  const skill = skills;
  return (
    <div id="skills" className="skills">
      <h2 className="title bigTilte">{t.skills}</h2>
      <div className="skills-container">
        <div className="section programmingLanguages">
          <h3 className="sub-title">{t.programmingLanguages}</h3>
          {skill.map((skill, index) => (
            <div key={index} className="item">
              <div className="item-info">
                <span>{skill.name}</span>
                <span>{skill.level}%</span>
              </div>
              <div className="item-bar">
                <motion.div
                  className="item-progress"
                  initial={{ width: 0 }}
                  animate={{
                    width: [
                      `${skill.level}%`,
                      `${100 - skill.level}%`,
                      `${skill.level}%`,
                    ],
                  }}
                  transition={{
                    duration: 1,
                    ease: "easeInOut",
                    repeat: Infinity,
                    repeatDelay: 10,
                  }}
                />
              </div>
            </div>
          ))}
        </div>
        <div className="section otherSkills">
          <h3 className="sub-title">{t.otherSkills}</h3>
          <ul>
            <li>Front-end: React, SCSS, Bootstrap, UI Design</li>
            <li>Back-end: Laravel Framework, API&apos;s, SQL, PHP</li>
            <li>
              Outils: VS Code, SublimeText, CMD, PowerShell, Git-Bash,
              GPT&apos;s
            </li>
            <li>3D Views avec Blender</li>
            <li>Microsoft Office</li>
            <li>UX/UI Design</li>
            <li>Résolution de problèmes</li>
            <li>Collaboration et travail en équipe</li>
            <li>Adaptabilité et communication</li>
          </ul>
        </div>
      </div>
    </div>
  );
}
