import Picture1 from "../assets/myPhoto1.png";
export default function Header(props) {
  const { t = {} } = props;
  return (
    <header id="about" className="header">
      <div className="image">
        <img src={Picture1} alt="mouad allaoui" />
      </div>
      <h1 className="name bigTilte">mouad allaoui</h1>
      <p className="job">{t.developerTitle}</p>
      <p className="desc">{t.availableNow}</p>
      <a href="#contact" className="contact">
        {t.contactMe}
      </a>
    </header>
  );
}
