// /* eslint-disable */
import { useEffect } from "react";
import { useAnimate, stagger, motion } from "framer-motion";

const staggerMenuItems = stagger(0.1, { startDelay: 0.15 });

function useMenuAnimation(isOpen) {
  const [scope, animate] = useAnimate();

  useEffect(() => {
    animate(
      ".menu-list",
      {
        clipPath: isOpen ? "inset(0% 0% 0% 0%)" : "inset(10% 50% 90% 50%)",
      },
      {
        type: "spring",
        bounce: 0,
        duration: 0.3,
      }
    );

    animate(
      ".item",
      isOpen
        ? { opacity: 1, scale: 1, filter: "blur(0px)" }
        : { opacity: 0, scale: 0.3, filter: "blur(20px)" },
      {
        duration: 0.2,
        delay: isOpen ? staggerMenuItems : 0,
      }
    );
  }, [isOpen]);

  return scope;
}

export default function Navbar(props) {
  const {
    t = {},
    toggleTheme = () => {},
    theme = "light",
    language = "fr",
    setLanguage = () => {},
    isMenuOpen = false,
    setIsMenuOpen = () => {},
  } = props;
  const scope = useMenuAnimation(isMenuOpen);
  return (
    <nav className="nav-bar" ref={scope}>
      <motion.h2
        initial={{ pathLength: 0 }}
        animate={{
          pathLength: [0, 0, 1, 1, 0],
          scale: [1, 1.2, 1.2, 1, 1],
          rotate: [0, 0, 5, 5, 0],
        }}
        transition={{ repeat: Infinity, duration: 2 }}
        className="nav-bar-logo"
      >
        Mouad Allaoui
      </motion.h2>
      <motion.ul
        initial={{ filter: "blur(15px)" }}
        animate={{ filter: "blur(0px)" }}
        transition={{ duration: 2 }}
        className="nav-bar-ul"
      >
        <li className="nav-bar-item">
          <a className="nav-bar-link" href="#about">
            {t.about}
          </a>
        </li>
        <li className="nav-bar-item">
          <a className="nav-bar-link" href="#skills">
            {t.skills}
          </a>
        </li>
        <li className="nav-bar-item">
          <a className="nav-bar-link" href="#experience">
            {t.experience}
          </a>
        </li>
        <li className="nav-bar-item">
          <a className="nav-bar-link" href="#education">
            {t.education}
          </a>
        </li>
        <li className="nav-bar-item">
          <a className="nav-bar-link" href="#languages">
            {t.languages}
          </a>
        </li>
        <li className="nav-bar-item">
          <a className="nav-bar-link" href="#contact">
            {t.contact}
          </a>
        </li>
        <li
          className="nav-bar-item download-cv"
          onClick={() => alert("cv not added yet")}
        >
          Download CV
        </li>
        <li className="nav-bar-item">
          <button onClick={toggleTheme} className="nav-bar-link">
            {theme === "dark" ? "light" : "dark"}
          </button>
        </li>
        <li className="nav-bar-item">
          <select
            value={language}
            onChange={(e) => setLanguage(e.target.value)}
            className="nav-bar-link"
          >
            <option value="fr">FR</option>
            <option value="en">EN</option>
          </select>
        </li>
      </motion.ul>
      <motion.button
        className="sm-screen-menu-btn"
        whileTap={{ scale: 0.97 }}
        onClick={() => setIsMenuOpen(!isMenuOpen)}
      >
        {isMenuOpen ? (
          <i className="fas fa-times"></i>
        ) : (
          <i className="fas fa-bars"></i>
        )}
      </motion.button>
      <ul
        style={{
          pointerEvents: isMenuOpen ? "auto" : "none",
          clipPath: "inset(10% 50% 90% 50%)",
        }}
        className="sm-screen-menu menu-list"
      >
        <li className="nav-bar-item item">
          <a className="nav-bar-link" href="#about">
            {t.about}
          </a>
        </li>
        <li className="nav-bar-item item">
          <a className="nav-bar-link" href="#skills">
            {t.skills}
          </a>
        </li>
        <li className="nav-bar-item item">
          <a className="nav-bar-link" href="#experience">
            {t.experience}
          </a>
        </li>
        <li className="nav-bar-item item">
          <a className="nav-bar-link" href="#education">
            {t.education}
          </a>
        </li>
        <li className="nav-bar-item item">
          <a className="nav-bar-link" href="#languages">
            {t.languages}
          </a>
        </li>
        <li className="nav-bar-item item">
          <a className="nav-bar-link" href="#contact">
            {t.contact}
          </a>
        </li>
        <li
          className="nav-bar-item item download-cv"
          onClick={() => alert("cv not added yet")}
        >
          Download CV
        </li>
        <li className="nav-bar-item item">
          <button onClick={toggleTheme} className="nav-bar-link">
            {theme === "dark" ? "light" : "dark"}
          </button>
        </li>
        <li className="nav-bar-item item">
          <select
            value={language}
            onChange={(e) => setLanguage(e.target.value)}
            className="nav-bar-link"
          >
            <option value="fr">FR</option>
            <option value="en">EN</option>
          </select>
        </li>
      </ul>
    </nav>
  );
}
